		right: 0; } }
@media(max-width: 1320px) and (min-width: 1200px) {
	.slider-prev {
		left: 0; }
	.slider-next {
		right: 0; } }
@media(max-width: 1060px) and (min-width: 992px) {
	.slider-prev {
		left: 0; }
	.slider-next {
		right: 0; } }
@media(max-width: 1399px) {
