.reviews {
	padding-top: 78px;
	padding-bottom: 90px;
	&-title {
		margin-bottom: 28px; }
	.section-subtitle {
		margin-bottom: 65px; } }
.review {
	&-video {
		text-align: center;
		img {
			max-width: 100%; } }
	&-text {
