		font-weight: 300;
		font-size: 17px;
		color: #2E4878;
		margin-bottom: 12px;
		span {
			font-weight: 500; }
		p {
			margin-bottom: 0; } } }
.slider .slider-item img {
	width: auto;
	display: inline; }
